import React, { Component } from 'react'

class TestList extends Component {
  constructor() {
    super()
    this.state = {
      listMembers: ''
    }
    // this.state.listMembers = this.state.listMembers.bind(this);
  }

  getMembers = () => {
    fetch("/api/memberList")
    .then((res) => {
      return res.json()
    })
    .then( (json) => {
      this.setState({
        listMembers: json.members
      })
    })
    .catch((err) => {
      console.log("error", err)
    })
  }

  render() {
    let members = this.state.listMembers;
    // console.log(members)
    
    if(this.state.listMembers){
      return (
        <div>
          <h1>OJF Members</h1>
          <ul className="memList">
            {
              members.map((member, index) =>
                <li key={index}>
                <div>
                  {`${member.merge_fields.FNAME} 
                  ${member.merge_fields.LNAME}:`}
                  <span className="rightColumn">
                  {`${member.email_address}`}
                  </span>
                </div>
                </li>
              )
            }
          </ul>
          {/* <button onClick={this.getMembers}>List All Members</button> */}
        </div>
      )
    }  
  
    return (
      <div>
        <h1>OJF Members</h1>
        <button onClick={this.getMembers}>List All Members</button>
      </div>
    )
  }
}

export default TestList